import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppRoutingModule } from './route/app-routing.module';
import { AppComponent } from './app.component';
import { SidebarComponent } from './components/common/sidebar/sidebar.component';
import { CommonLayoutComponent } from './components/common/common-layout/common-layout.component';
import { LoginComponent } from './components/common/login/login.component';
import { SharedModule } from './components/shared/shared.module';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AddUserComponent } from './components/organizationAdmin/add-user/add-user.component';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { ApiInterceptorInterceptor } from './api-interceptor.interceptor';
import { NgxPaginationModule } from 'ngx-pagination';
import { ForgetPasswordComponent } from './components/common/forget-password/forget-password.component';
import { OtpComponent } from './components/common/otp/otp.component';
import { logGuardGuard } from './guards/log-guard.guard';
import { UnAuthorisedComponent } from './components/un-authorised/un-authorised.component';
import { PageNotFoundComponent } from './components/page-not-found.component';
import { TreeModule } from 'primeng/tree';
import { DatePipe } from '@angular/common';
import { RecaptchaFormsModule, RecaptchaModule } from 'ng-recaptcha';

@NgModule({
  declarations: [
    AppComponent,
    SidebarComponent,
    CommonLayoutComponent,
    LoginComponent,
    AddUserComponent,
    ForgetPasswordComponent,
    OtpComponent,
    UnAuthorisedComponent,
    PageNotFoundComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    SharedModule,
    BrowserAnimationsModule,
    NgxPaginationModule,
    TreeModule,
    RecaptchaModule,
    RecaptchaFormsModule,
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ApiInterceptorInterceptor,
      multi: true,
    },
    DatePipe,
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
