import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpErrorResponse,
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { MessageService } from './services/message.service';

@Injectable()
export class ApiInterceptorInterceptor implements HttpInterceptor {
  constructor(
    private router: Router,
    private spinner: NgxSpinnerService,
    private messageService: MessageService
  ) {}

  intercept(
    request: HttpRequest<unknown>,
    next: HttpHandler
  ): Observable<HttpEvent<unknown>> {
    // return next.handle(request);
    const token: any = localStorage.getItem('authToken');
    let newRequest;

    if (token && !request.url.includes('/LOGIN/api/userlogin'))
      newRequest = request.clone({
        setHeaders: {
          Authorization: `Bearer ${token}`,
          'Cache-Control': 'no-cache, no-store',
          Pragma: 'no-cache',
        },
      });
    else newRequest = request;

    return next.handle(newRequest).pipe(
      catchError((error: HttpErrorResponse) => {
        // this.spinner.hide();
        if (error instanceof HttpErrorResponse && error.status == 401) {
          this.messageService.errorToast('Your session has been expired.');
          // console.error('An error occurred:', error);
          this.spinner.hide();
          localStorage.clear();
          this.router.navigate(['/login']);
        } else if (error.status == 403) {
          console.log(error);

          this.messageService.errorToast(error.error);
          this.spinner.hide();
        } else {
          this.spinner.hide();
          this.messageService.errorToast(error?.error?.errorMessage);
        }
        return throwError(error);
      })
    );
  }
}
