import { Component } from '@angular/core';
import { Observable, timer } from 'rxjs';
import { BehaviorSubjectCollectionService } from './services/behaviorSubjectCollection.service';
import { CommonService } from './services/common/common.service';
import { UserAccessManagementService } from './services/user-access-management.service';
import { NgxSpinner, NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  title = 'ESG';
  timerUnsubscribe: any;
  validRoute: any[] = [];
  sidebarMenu: any[] = [];
  flag: boolean = false;

  constructor(
    private behaviorSubjectCollection: BehaviorSubjectCollectionService,
    private commonService: CommonService,
    private _userAccessSvc: UserAccessManagementService,
    private spinner: NgxSpinnerService
  ) {
    if (this.commonService.isUserLoggedIn()) {
      this.count();
    }
    this.commonService.userLoggedIn.subscribe((res: any) => {
      this.count();
    });
  }

  topToWindow() {
    window.scrollTo(0, 0);
  }

  count() {
    let userData: any = JSON.parse(localStorage.getItem('userData')!);
    let accessTokenExpirationTime = userData?.accessTokenExpirationTime;

    const twoMinutesInMilliseconds = 2 * 60 * 1000; // 2 minutes in milliseconds

    // Calculate the time to wait before triggering the action
    if (accessTokenExpirationTime) {
      const timeToWait =
        new Date(accessTokenExpirationTime).getTime() -
        new Date().getTime() -
        twoMinutesInMilliseconds;

      // console.log(timeToWait, 'time to wait');

      // Create a timer observable
      const timer$ = timer(timeToWait);

      // Subscribe to the timer observable and perform the action
      this.timerUnsubscribe = timer$.subscribe(() => {
        // Your action to perform 2 minutes before the target date and time
        this.generateRefreshToken();
      });
    }
  }

  generateRefreshToken() {
    this.spinner.show();
    this.timerUnsubscribe.unsubscribe();
    this.commonService.GenerateRefreshToken().subscribe(
      (res: any) => {
        var obj = JSON.parse(localStorage.getItem('userData')!);
        obj.accessToken = res.accessToken;
        obj.refreshToken = res.refreshToken;
        obj.accessTokenExpirationTime = res.accessTokenExpirationTime;
        localStorage.setItem('userData', JSON.stringify(obj));
        localStorage.setItem('authToken', res?.accessToken);
        this.count();
        this.spinner.hide();
      },
      (err: any) => {
        console.log(err);
        this.spinner.hide();
      }
    );
  }
}
