import { ChangeDetectorRef, Component } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { CommonService } from 'src/app/services/common/common.service';
import { MessageService } from 'src/app/services/message.service';
import { ConfirmationDialogComponent } from '../../shared/confirmation-dialog/confirmation-dialog.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss'],
})
export class SidebarComponent {
  imageUrl: any =
    'https://icon-library.com/images/generic-user-icon/generic-user-icon-9.jpg';
  currentUrl!: any;
  userName!: String;
  validRoute: any[] = [];

  sidebarMenu: any[] = [];
  userRole: any;
  profile: any;

  constructor(
    private changeDetector: ChangeDetectorRef,
    private commonService: CommonService,
    private router: Router,
    private spinner: NgxSpinnerService,
    private messageService: MessageService,
    private _dialog: MatDialog
  ) {}

  ngOnInit(): void {
    this.userName = JSON.parse(localStorage.getItem('userData')!).username;
    this.currentUrl = window.location.pathname;
    let image = localStorage.getItem('imageUrl');
    if (image && this.currentUrl != '/setup')
      this.imageUrl = localStorage.getItem('imageUrl');
    this.changeDetector.detectChanges();
    let userRole = JSON.parse(localStorage.getItem('userData')!)?.roleName;
    this.userRole =
      userRole == 'SuperAdmin' || userRole == 'Admin' ? userRole : 'user';

    this.sidebarMenu = JSON.parse(localStorage.getItem('validRoutes')!);
    // console.log('sidebar', this.sidebarMenu);
    if (this.userRole != 'user') {
      this.sidebarMenu = CommonService.superAdminMenu;
    } else {
      this.sidebarMenu = this.commonService.userMenu;
    }

    if (JSON.parse(localStorage.getItem('userData')!).Photo) {
      this.profile =
        environment.apiHostName +
        '/' +
        JSON.parse(localStorage.getItem('userData')!).Photo;
    }
  }

  logOut() {
    var dialogRef = this._dialog.open(ConfirmationDialogComponent, {
      width: '23.472vw',
      // height: '161px',
      data: {
        header: 'Logout ?',
        data: {
          bodyData: `Are you sure, you want to Logout ?`,
        },
        button: {
          isSubmit: true,
          submitbtnText: 'Logout',
          isCancel: true,
          isDelete: false,
        },
      },
    });
    dialogRef.disableClose = true;

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result == 'ok') {
        this.spinner.show();
        this.commonService.logout().subscribe((res: any) => {
          localStorage.clear();
          this.router.navigate(['/login']);
          this.spinner.hide();
        });
      }
    });
  }

  routeChange(path: any) {
    this.currentUrl = path;
    let image = localStorage.getItem('imageUrl');
    if (image && (path != '/setup' || path != '404'))
      this.imageUrl = localStorage.getItem('imageUrl');
    this.profile =
      environment.apiHostName +
      '/' +
      JSON.parse(localStorage.getItem('userData')!).Photo;
  }

  cl() {
    console.log((this.sidebarMenu = this.sidebarMenu));
  }
}
