  userLogin(formData: any) {
    const requestBody = {
      username: formData.username,
      // password: formData.password,
      password: this.encryptData(formData.password.trim()),
    };
    return this.http.post(
      `${URLS.baseApiHostUrl}${URLS.login.endpoint}${URLS.login.methods.userLogin}`,
      requestBody
    );
  }

  logout() {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.login.endpoint}${URLS.login.methods.logout}`
    );
  }

  getDomain() {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Master.endpoint}${URLS.Master.methods.GetDomain}`
    );
  }

  changePassword(requestBody: any) {
    return this.http.post(
      `${URLS.baseApiHostUrl}${URLS.login.endpoint}${URLS.login.methods.changePassword}`,
      requestBody
    );
  }
  sendOTP(requestBody: any) {
    return this.http.post(
      `${URLS.baseApiHostUrl}${URLS.login.endpoint}${URLS.login.methods.SendOTP}`,
      requestBody
    );
  }
  VerifyOTP(requestBody: any) {
    return this.http.post(
      `${URLS.baseApiHostUrl}${URLS.login.endpoint}${URLS.login.methods.ValidateOtp}`,
      requestBody
    );
  }
  GenerateRefreshToken() {
    var token: any = JSON.parse(localStorage.getItem('userData')!);
    const requestBody = {
      email: token.username,
      refreshToken: token.refreshToken,
      accessToken: token.accessToken,
    };
    return this.http.post(
      `${URLS.baseApiHostUrl}${URLS.login.endpoint}${URLS.login.methods.refreshToken}`,
      requestBody
    );
  }

  searchAddress(
    city: string,
    state: string,
    country: string,
    postalCode: string
  ) {
    let paramData: any = {};
    if (city) paramData['city'] = city;
    if (state) paramData['state'] = state;
    if (country) paramData['country'] = country;
    if (postalCode) paramData['postalcode'] = postalCode;
    paramData['format'] = 'jsonv2';
    paramData['addressdetails'] = 1;

    return this.http.get(`${URLS.baseAddressUrl}${URLS.addressUrl.endpoint}`, {
      params: paramData,
    });
  }

  getCountryList() {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Master.endpoint}${URLS.Master.methods.GetAllCountryMaster}`
    );
  }

  getStateList(countryId: any) {
    let param = {
      CountryId: countryId,
    };
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Master.endpoint}${URLS.Master.methods.GetStateByCountryId}`,
      { params: param }
    );
  }

  getAllCountryStateList() {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Master.endpoint}${URLS.Master.methods.GetAllStateMaster}`
    );
  }

  getCityList(stateId: any) {
    let param = {
      StateId: stateId,
    };
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Master.endpoint}${URLS.Master.methods.GetCityByStateId}`,
      { params: param }
    );
  }

  getAllCountryStateCityList() {
    return this.http.get(
      `${URLS.baseApiHostUrl}${URLS.Master.endpoint}${URLS.Master.methods.GetAllCityMaster}`
    );
  }

  getReqLoginUserName() {
    if (localStorage.getItem('userData'))
      return JSON.parse(localStorage.getItem('userData')!).username;
  }

  getRole() {
    if (localStorage.getItem('userData'))
      return JSON.parse(localStorage.getItem('userData')!).roleName;
  }

  isUserLoggedIn(): boolean {
    ///return true if we have token in local storage
    if (localStorage.getItem('authToken')) return true;
    else return false;
  }
  /////to encrypt data
  encryptData(data: any) {
    var rsa: any = forge.pki.publicKeyFromPem(this.publicKey);
    return window.btoa(rsa.encrypt(data));
  }

